const state = {
    statusLoadingChatApp: true,
    scrollOutOnce: true,
};

const getters = {
    getStatusLoadingChatApp: (state) => state.statusLoadingChatApp,
    getScrollOutOnce: (state) => state.scrollOutOnce
};

const actions = {
    setStatusLoadingChatApp({ commit }, payload){
        setTimeout(() => {
            commit('SET_STATUS_LOADING_CHAT_APP', payload)
        }, 3000);
    },
};

const mutations = {
    SET_STATUS_LOADING_CHAT_APP: (state, data) => state.statusLoadingChatApp = data.status,
};

export default {
    state,
    getters,
    actions,
    mutations
}